import React, {Component} from 'react';

class Contact extends Component{
    render(){
        return(
            <footer className="footer text-center" id="contact">
            <div className="container">
              <div className="row">
        
        
                
                <div className="col-lg-12 text-center">
                  <h4 className="text-uppercase mb-4">Get in Touch!</h4>
                  
                  <a className="btn btn-outline-light btn-social mx-1" href="mailto:komalbhuvanagiri@gmail.com">
                    <i className="fas fa-envelope"></i>
                  </a>
                  <a className="btn btn-outline-light btn-social mx-1" href="https://www.linkedin.com/in/komalvanamala" target="_blank">
                    <i className="fab fa-fw fa-linkedin-in"></i>
                  </a>
                
                <a className="btn btn-outline-light btn-social mx-1" href="https://komalbhuv.github.io" target="_blank">
                    <i className="fab fa-github"></i>
                  </a>
                </div>
        
        
              </div>
            </div>
           
          </footer>
          
          
      
        );
    }
    
} 

export default Contact;