import React, {Component} from 'react';

class Header extends Component{
    render(){
        return(
            <header className="masthead bg-primary text-white text-center">
                <div className="container d-flex align-items-center flex-column">
                    <img className="masthead-avatar mb-5" src="img/avataaars.png" alt=""/>
                    <h1 className="masthead-heading text-uppercase mb-0">Komal Bhuvanagiri</h1>
                        <div className="divider-custom divider-light">
                            <div className="divider-custom-line"></div>
                            <div className="divider-custom-icon">
                            <i className="fas fa-star"></i>
                            </div>
                            <div className="divider-custom-line"></div>
                        </div>  
                    <p className="masthead-subheading font-weight-normal mb-0">INQUISITIVE LEARNER | ASPIRING DEVELOPER | PROBLEM SOLVER | TRAINED ENGINEER</p>
                </div>
             </header>
        );
    }
    
} 

export default Header;