import React, {Component} from 'react';

class About extends Component{
    render(){
        return(
            <section className="page-section mb-0" id="about">
    <div className="container">

      
      <h2 className="page-section-heading text-center text-uppercase ">About</h2>

      
      <div className="divider-custom ">
        <div className="divider-custom-line"></div>
        <div className="divider-custom-icon">
          <i className="fas fa-star"></i>
        </div>
        <div className="divider-custom-line"></div>
      </div>

      
      <div className="row">
        <div className="col-lg-4 ml-auto">
          <p className="lead">
          Enthusiastic and self-motivated professional with passion to become a software Engineer. 
          Zealous to learn new technologies and acquire expertise in the field of software development. 
          
          </p>
        </div>
        <div className="col-lg-4 mr-auto">
          <p className="lead">
          Strong time-management, creative problem solving and interpersonal skills are some of my key abilities. 
          Seeking to take the next career step with a respected and influential organization.
          </p>
        </div>
      </div>

      
      <div className="text-center mt-4">
        <a className="btn btn-xl" href="https://drive.google.com/open?id=1cL67oQI4akabEZaLMox80tBTMevOZGQe" target="_blank">
          <i className="fas fa-download mr-2"></i>
          Download CV!
        </a>
      </div>

    </div>
  </section>
        );
    }
    
} 

export default About;